.sidebar-section{
    display: flex;
    background-color: #030303da;
    
}

.sidebar{
    height: 100vh;
    background-color: #030303da;
    width:95px;
    position: fixed;
}

.container-fluid{
    padding-left: 0px!important;
    padding-right:0px!important ;
    overflow: hidden;
}

.sidebar-expand{
    width: 230px;
}

.icon-for-sidebar-expand-and-collapse{
    display: flex;
    align-items: right;
    justify-content:right;
    cursor: pointer;
}

.icon-for-sidebar-expand-and-collapse p svg{
    background-color: #19092c;
    padding: 5px;
    color: white;
    border-radius: 5px;
    margin-top: 50px;

}

.container{
    margin-left: 250px;
}

@media (max-width:1000px){
    .sidebar{
        display: none;
    }
}
