.section-title h5{
    text-align: center;
}
.line{
    border-bottom: 3px solid yellowgreen;
    display: inline-block; 
    padding-bottom: 5px;
}

.techstack-section{
    margin-top: 40px;
    height: auto;
    margin-bottom: 40px;
}

.text-content{
    padding: 5px;
    color:black;
    margin-top: 50px;
    box-shadow: 14px 9px 9px -2px rgba(230,224,224,0.86)
    ;
    -webkit-box-shadow: 15px 9px 9px -2px rgba(230, 224, 224, 0.86);
    -moz-box-shadow:15px 9px 9px -2px rgba(230, 224, 224, 0.86);
}

.text-content p{
    text-align: center;
    padding-top: 30px;
    font-size: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.text-content:hover{
    background-color: #FF845C;
    color: white;
    cursor: pointer;
    transition-duration: 1s;
    box-shadow: -6px 11px 5px -4px rgba(0,0,0,0.42)
    ;
    -webkit-box-shadow: -6px 11px 5px -4px rgba(0,0,0,0.42);
    -moz-box-shadow:-6px 11px 5px -4px rgba(0,0,0,0.42);

}

.tech-number{
    border-radius: 90px;
    height:50px;
    width: 50px;
    color: white;
    text-align: center;
    align-items: center;
    justify-content: center;
    display: block;
    margin: 0 auto;
    margin-top: -30px;
    line-height: 50px;
}

.load-More-tech-stack{
    background-color: tomato;
    color: white;
    padding: 10px;
    width: 150px;
    text-align: center;
    justify-content: center;
    display: table;
    margin-left: auto;
    margin-right: auto;
    margin-top:35px;
    border-radius: 5px;
    margin-bottom: 40px;
    cursor: pointer;
}

.text-content-marked{
    background-color: tomato;
    color: white;
}

.load-More-tech-stack:hover{
    background-color: brown;
    transition: 1s;
}